body {
  margin: 0;
  padding-top: 10px;
  background-color: white;
  margin: 2%;
}

.input {
  display: none;
}


.priority {
  max-width: 50px;
}

.label {
    margin: 0 10px;
    font-size: 16px;
    font-family: sans-serif;
    color: white;
}

.toggle-container {
    display: inline-block;
}

.input + .toggle {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.input + .toggle {
  padding: 2px;
  width: 60px;
  height: 30px;
  background-color: #dddddd;
  border-radius: 30px;
  -webkit-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
  display: inline-block;
  vertical-align: middle;
}
.input + .toggle:before,
.input + .toggle:after {
  display: block;
  position: absolute;
  content: "";
}
.input + .toggle:before {
  top: 2px;
  left: 2px;
  bottom: 2px;
  right: 2px;
  background-color: #fff;
  border-radius: 30px;
  -webkit-transition: background 0.4s;
  -o-transition: background 0.4s;
  transition: background 0.4s;
}
.input + .toggle:after {
  top: 4px;
  left: 4px;
  bottom: 4px;
  width: 26px;
  background-color: #dddddd;
  border-radius: 26px;
  -webkit-transition: margin 0.4s, background 0.4s;
  -o-transition: margin 0.4s, background 0.4s;
  transition: margin 0.4s, background 0.4s;
}
.input:checked + .toggle {
  background-color: #8ce196;
}
.input:checked + .toggle:after {
  margin-left: 30px;
  background-color: #8ce196;
}


iframe {
  border-width: 3px;
  width:95%;
  height: 300px;
}

table {
  margin-left: 2%;
  width: 90%;
  max-width: 800px;
}



